import { CheckCircle } from 'lucide-react';

interface ToggleGroupProps {
  title: string;
  icon?: React.ComponentType<{ className?: string }>;
  options: string[];
  selectedOptions: string[];
  onChange: (options: string[]) => void;
  className?: string;
  columns?: number;
  disabled?: boolean;
}

export function ToggleGroup({ 
  title, 
  icon: Icon, 
  options, 
  selectedOptions, 
  onChange, 
  className = '',
  columns = 2,
  disabled = false
}: ToggleGroupProps) {
  // Ensure selectedOptions is always an array
  const safeSelectedOptions = Array.isArray(selectedOptions) ? selectedOptions : [];
  
  const toggleOption = (option: string) => {
    if (disabled) return;
    
    // For single selection behavior, always set the selected option
    const newOptions = [option];
    onChange(newOptions);
  };

  const gridCols = columns === 1 ? 'grid-cols-1' : 
                  columns === 3 ? 'grid-cols-3' : 
                  columns === 4 ? 'grid-cols-4' : 'grid-cols-2';

  return (
    <div className={`${title ? 'space-y-3' : ''} ${className}`}>
      {title && (
        <div className="flex items-center gap-2 text-slate-300">
          {Icon ? (
            <Icon className="w-4 h-4 text-blue-400" />
          ) : (
            <CheckCircle className="w-4 h-4 text-blue-400" />
          )}
          <span className="text-sm font-medium">{title}</span>
        </div>
      )}
      
      <div className={`grid gap-2 ${gridCols}`}>
        {options.map((option) => {
          const isSelected = safeSelectedOptions.includes(option);
          
          return (
            <button
              key={option}
              onClick={() => toggleOption(option)}
              disabled={disabled}
              className={`
                py-2 px-3 text-sm font-medium rounded-lg border transition-all duration-200
                ${disabled
                  ? 'bg-slate-800/30 border-slate-700/30 text-slate-500 cursor-not-allowed opacity-60'
                  : isSelected
                    ? 'bg-blue-600 border-blue-600 text-white shadow-md'
                    : 'bg-slate-700/60 border-slate-700/60 text-slate-400 hover:bg-slate-700/60 hover:border-slate-600 hover:text-slate-300'
                }
              `}
            >
              {option}
            </button>
          );
        })}
      </div>
    </div>
  );
}